.lang-swith {
  @include r(md) {
    margin-right: 40px; }
  &__control {
    cursor: pointer;
    .ico {
      vertical-align: middle;
      font-size: 24px; } }
  &__lang {
    margin-right: .5em;
    text-transform: uppercase;
    text-decoration: dashed; } }

.burger {
  @include r(md) {
    display: none; } }

.menu {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin: 0;
  list-style: none;
  &__link {
    text-decoration: none;
    transition: color .2s;
    &:hover {
      color: $c-brand; } }
  @include r(sm) {
    flex-direction: row; } }
