  min-height: 100vh;
  padding: 120px 0 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.swiper-container-fade .swiper-slide:not(.swiper-slide-active) {
  opacity: 0 !important;
}

.service .swiper-pagination {
  position: static;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 120px;
  bottom: 0;
}

.service .pagination-icon:nth-child(1) .pagination-icon__wrap:before {
  background-image: url("../img/ico/service-ico-1.svg");
}

.service .pagination-icon:nth-child(2) .pagination-icon__wrap:before {
  background-image: url("../img/ico/service-ico-2.svg");
}

.service .pagination-icon:nth-child(3) .pagination-icon__wrap:before {
  background-image: url("../img/ico/service-ico-3.svg");
}

.service .pagination-icon:nth-child(4) .pagination-icon__wrap:before {
  background-image: url("../img/ico/service-ico-4.svg");
}

.pagination-icon {
  width: auto;
  height: auto;
  border-radius: 0;
  opacity: 1;
  background: transparent;
  flex: 2 1 auto;
  display: flex;
  align-items: center;
}

.pagination-icon:last-child {
  flex: 1 1;
}

.pagination-icon:last-child .pagination-icon__timer {
  display: none;
}

.pagination-icon__wrap {
  max-width: 120px;
  min-width: 40px;
  position: relative;
  flex: 2 1;
}

.pagination-icon__wrap:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 50% 45%;
  background-size: 50% auto;
}

.pagination-icon__timer {
  display: block;
  height: 6px;
  flex: 1 1;
  background-color: #fff;
  margin: 0 30px;
  border-radius: 2px;
  background: linear-gradient(to right, #e41139 50%, #fff 50%) no-repeat;
  background-size: 200% auto;
  background-position-x: 101%;
}

.process .swiper-pagination-bullet-active .pagination-icon__timer {
  animation-name: progress-time;
  animation-timing-function: linear;
}

.svg-st-out {
  fill: transparent;
  stroke: #fff;
}

.svg-st-in {
  transition: all .2s;
}

.swiper-pagination-bullet-active .pagination-icon__wrap {
  filter: drop-shadow(0px 0px 10px rgba(228, 17, 57, 0.7));
  animation: pagination 3s;
}

.swiper-pagination-bullet-active .svg-st-out {
  stroke: transparent;
  fill: #2e3542;
}

.swiper-pagination-bullet-active .svg-st-in {
  fill: #e41139;
}

@keyframes progress-time {
  0% {
    background-position-x: 101%;
  }
  100% {
    background-position-x: 0%;
  }
}

@keyframes pagination {
  0% {
    transform: scale(1);
  }
  15% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
